'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'

interface LocationPoint {
  id: string
  employeeId: string
  employeeName: string
  lat: number
  lng: number
  timestamp: string
  status: 'valid' | 'suspicious' | 'blocked'
  accuracy: number
  address: string
}

interface Geofence {
  id: string
  name: string
  centerLat: number
  centerLng: number
  radius: number
  active: boolean
}

export function LocationTrackingMap() {
  const [locations, setLocations] = useState<LocationPoint[]>([])
  const [geofences, setGeofences] = useState<Geofence[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedLocation, setSelectedLocation] = useState<LocationPoint | null>(null)

  useEffect(() => {
    const fetchLocationData = async () => {
      setLoading(true)
      try {
        // Simulate API delay
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // Mock location data
        const mockLocations: LocationPoint[] = [
          {
            id: '1',
            employeeId: 'EMP001',
            employeeName: 'John Smith',
            lat: 40.7128,
            lng: -74.0060,
            timestamp: new Date().toISOString(),
            status: 'valid',
            accuracy: 3.2,
            address: 'Office Building A, Floor 3'
          },
          {
            id: '2',
            employeeId: 'EMP002',
            employeeName: 'Sarah Johnson',
            lat: 40.7130,
            lng: -74.0058,
            timestamp: new Date(Date.now() - 300000).toISOString(),
            status: 'suspicious',
            accuracy: 15.7,
            address: 'Office Building A, Floor 2'
          },
          {
            id: '3',
            employeeId: 'EMP003',
            employeeName: 'Mike Wilson',
            lat: 40.7125,
            lng: -74.0062,
            timestamp: new Date(Date.now() - 600000).toISOString(),
            status: 'blocked',
            accuracy: 50.0,
            address: 'Suspicious Location'
          }
        ]

        const mockGeofences: Geofence[] = [
          {
            id: '1',
            name: 'Main Office',
            centerLat: 40.7128,
            centerLng: -74.0060,
            radius: 100,
            active: true
          },
          {
            id: '2',
            name: 'Parking Area',
            centerLat: 40.7125,
            centerLng: -74.0065,
            radius: 50,
            active: true
          }
        ]
        
        setLocations(mockLocations)
        setGeofences(mockGeofences)
      } catch (error) {
        console.error('Failed to fetch location data:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchLocationData()
    
    // Simulate real-time location updates
    const interval = setInterval(() => {
      setLocations(prev => prev.map(loc => ({
        ...loc,
        timestamp: Math.random() > 0.8 ? new Date().toISOString() : loc.timestamp,
        lat: loc.lat + (Math.random() - 0.5) * 0.0001,
        lng: loc.lng + (Math.random() - 0.5) * 0.0001
      })))
    }, 5000)

    return () => clearInterval(interval)
  }, [])

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'valid':
        return 'bg-green-500'
      case 'suspicious':
        return 'bg-yellow-500'
      case 'blocked':
        return 'bg-red-500'
      default:
        return 'bg-gray-500'
    }
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'valid':
        return <Badge className="bg-green-100 text-green-800 border-green-200">Valid</Badge>
      case 'suspicious':
        return <Badge className="bg-yellow-100 text-yellow-800 border-yellow-200">Suspicious</Badge>
      case 'blocked':
        return <Badge className="bg-red-100 text-red-800 border-red-200">Blocked</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  if (loading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Location Tracking</CardTitle>
          <CardDescription>Loading location data...</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center h-96">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      {/* Location Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Active Locations</p>
                <p className="text-2xl font-bold text-blue-600">{locations.length}</p>
              </div>
              <div className="w-3 h-3 bg-blue-500 rounded-full animate-pulse"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Valid Locations</p>
                <p className="text-2xl font-bold text-green-600">
                  {locations.filter(l => l.status === 'valid').length}
                </p>
              </div>
              <div className="w-3 h-3 bg-green-500 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Suspicious</p>
                <p className="text-2xl font-bold text-yellow-600">
                  {locations.filter(l => l.status === 'suspicious').length}
                </p>
              </div>
              <div className="w-3 h-3 bg-yellow-500 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Blocked</p>
                <p className="text-2xl font-bold text-red-600">
                  {locations.filter(l => l.status === 'blocked').length}
                </p>
              </div>
              <div className="w-3 h-3 bg-red-500 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Map Placeholder */}
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <div>
                <CardTitle>Real-time Location Map</CardTitle>
                <CardDescription>
                  Live GPS tracking with geofence monitoring
                </CardDescription>
              </div>
              <Badge variant="outline" className="bg-green-50 text-green-700 border-green-200">
                <div className="w-2 h-2 bg-green-500 rounded-full mr-2 animate-pulse"></div>
                Live
              </Badge>
            </div>
          </CardHeader>
          <CardContent>
            <div className="relative h-80 bg-gray-100 rounded-lg overflow-hidden">
              {/* Map placeholder with location points */}
              <img 
                src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/8cc2af4d-1a3c-4e23-9dff-fdc8c64355ed.png" 
                alt="Interactive GPS map showing real-time employee locations with geofence boundaries and security status indicators"
                className="w-full h-full object-cover"
                onError={(e) => {
                  e.currentTarget.style.display = 'none'
                  e.currentTarget.nextElementSibling?.classList.remove('hidden')
                }}
              />
              <div className="hidden absolute inset-0 flex items-center justify-center bg-gray-100">
                <p className="text-gray-500">Map visualization would appear here</p>
              </div>
              
              {/* Location points overlay */}
              <div className="absolute inset-0">
                {locations.map((location, index) => (
                  <div
                    key={location.id}
                    className={`absolute w-4 h-4 rounded-full ${getStatusColor(location.status)} cursor-pointer transform -translate-x-2 -translate-y-2 animate-pulse`}
                    style={{
                      left: `${20 + index * 25}%`,
                      top: `${30 + index * 15}%`
                    }}
                    onClick={() => setSelectedLocation(location)}
                    title={`${location.employeeName} - ${location.status}`}
                  />
                ))}
              </div>
              
              {/* Geofence indicators */}
              {geofences.map((fence, index) => (
                <div
                  key={fence.id}
                  className="absolute border-2 border-blue-400 border-dashed rounded-full opacity-50"
                  style={{
                    left: `${15 + index * 30}%`,
                    top: `${25 + index * 20}%`,
                    width: '80px',
                    height: '80px'
                  }}
                />
              ))}
            </div>
          </CardContent>
        </Card>

        {/* Location Details */}
        <Card>
          <CardHeader>
            <CardTitle>Location Details</CardTitle>
            <CardDescription>
              Detailed information about tracked locations
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {locations.map((location) => (
                <div 
                  key={location.id} 
                  className={`p-4 border rounded-lg cursor-pointer transition-colors ${
                    selectedLocation?.id === location.id ? 'bg-blue-50 border-blue-200' : 'hover:bg-gray-50'
                  }`}
                  onClick={() => setSelectedLocation(location)}
                >
                  <div className="flex items-start justify-between mb-2">
                    <div>
                      <h4 className="font-medium text-gray-900">{location.employeeName}</h4>
                      <p className="text-sm text-gray-500">{location.employeeId}</p>
                    </div>
                    {getStatusBadge(location.status)}
                  </div>
                  
                  <div className="space-y-1 text-sm text-gray-600">
                    <p>Address: {location.address}</p>
                    <p>Coordinates: {location.lat.toFixed(6)}, {location.lng.toFixed(6)}</p>
                    <p>Accuracy: {location.accuracy}m</p>
                    <p>Last Update: {new Date(location.timestamp).toLocaleString()}</p>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Geofence Management */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle>Geofence Management</CardTitle>
              <CardDescription>
                Configure and monitor location boundaries
              </CardDescription>
            </div>
            <Button variant="outline" size="sm">
              Add Geofence
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {geofences.map((fence) => (
              <div key={fence.id} className="flex items-center justify-between p-4 border rounded-lg">
                <div>
                  <h4 className="font-medium text-gray-900">{fence.name}</h4>
                  <p className="text-sm text-gray-500">
                    Center: {fence.centerLat.toFixed(6)}, {fence.centerLng.toFixed(6)} | Radius: {fence.radius}m
                  </p>
                </div>
                <div className="flex items-center gap-2">
                  <Badge variant={fence.active ? "default" : "secondary"}>
                    {fence.active ? "Active" : "Inactive"}
                  </Badge>
                  <Button variant="outline" size="sm">
                    Edit
                  </Button>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
