'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface SecurityAlert {
  id: string
  type: 'mock_location' | 'gps_spoofing' | 'vpn_detected' | 'root_access' | 'network_mismatch'
  severity: 'low' | 'medium' | 'high' | 'critical'
  employeeId: string
  employeeName: string
  deviceInfo: string
  timestamp: string
  location: {
    lat: number
    lng: number
    address: string
  }
  description: string
  status: 'active' | 'resolved' | 'investigating'
}

export function SecurityAlertsPanel() {
  const [alerts, setAlerts] = useState<SecurityAlert[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchAlerts = async () => {
      setLoading(true)
      try {
        // Simulate API delay
        await new Promise(resolve => setTimeout(resolve, 800))
        
        // Mock security alerts data
        const mockAlerts: SecurityAlert[] = [
          {
            id: '1',
            type: 'mock_location',
            severity: 'critical',
            employeeId: 'EMP003',
            employeeName: 'Mike Wilson',
            deviceInfo: 'OnePlus 11',
            timestamp: new Date().toISOString(),
            location: {
              lat: 40.7128,
              lng: -74.0060,
              address: 'Fake GPS Location Detected'
            },
            description: 'Mock location provider detected. Device is using fake GPS coordinates.',
            status: 'active'
          },
          {
            id: '2',
            type: 'vpn_detected',
            severity: 'high',
            employeeId: 'EMP002',
            employeeName: 'Sarah Johnson',
            deviceInfo: 'iPhone 14 Pro',
            timestamp: new Date(Date.now() - 300000).toISOString(),
            location: {
              lat: 40.7130,
              lng: -74.0058,
              address: 'Office Building A, Floor 2'
            },
            description: 'VPN connection detected. IP geolocation does not match GPS coordinates.',
            status: 'investigating'
          },
          {
            id: '3',
            type: 'gps_spoofing',
            severity: 'high',
            employeeId: 'EMP004',
            employeeName: 'David Brown',
            deviceInfo: 'Samsung Galaxy A54',
            timestamp: new Date(Date.now() - 600000).toISOString(),
            location: {
              lat: 40.7125,
              lng: -74.0062,
              address: 'Suspicious Location Jump'
            },
            description: 'Impossible location change detected. Movement speed exceeds physical limits.',
            status: 'resolved'
          }
        ]
        
        setAlerts(mockAlerts)
      } catch (error) {
        console.error('Failed to fetch security alerts:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchAlerts()
    
    // Simulate real-time updates
    const interval = setInterval(() => {
      // Randomly update alert statuses or add new alerts
      setAlerts(prev => prev.map(alert => ({
        ...alert,
        timestamp: Math.random() > 0.9 ? new Date().toISOString() : alert.timestamp
      })))
    }, 10000)

    return () => clearInterval(interval)
  }, [])

  const getSeverityBadge = (severity: string) => {
    switch (severity) {
      case 'critical':
        return <Badge className="bg-red-600 text-white">Critical</Badge>
      case 'high':
        return <Badge className="bg-orange-500 text-white">High</Badge>
      case 'medium':
        return <Badge className="bg-yellow-500 text-white">Medium</Badge>
      case 'low':
        return <Badge className="bg-blue-500 text-white">Low</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'active':
        return <Badge className="bg-red-100 text-red-800 border-red-200 animate-pulse">Active</Badge>
      case 'investigating':
        return <Badge className="bg-yellow-100 text-yellow-800 border-yellow-200">Investigating</Badge>
      case 'resolved':
        return <Badge className="bg-green-100 text-green-800 border-green-200">Resolved</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  const getAlertTypeLabel = (type: string) => {
    switch (type) {
      case 'mock_location':
        return 'Mock Location Detected'
      case 'gps_spoofing':
        return 'GPS Spoofing'
      case 'vpn_detected':
        return 'VPN/Proxy Detected'
      case 'root_access':
        return 'Root Access Detected'
      case 'network_mismatch':
        return 'Network Location Mismatch'
      default:
        return 'Security Violation'
    }
  }

  if (loading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Security Alerts</CardTitle>
          <CardDescription>Loading security monitoring data...</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center h-32">
            <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-red-600"></div>
          </div>
        </CardContent>
      </Card>
    )
  }

  const activeAlerts = alerts.filter(alert => alert.status === 'active')
  const criticalAlerts = alerts.filter(alert => alert.severity === 'critical')

  return (
    <div className="space-y-6">
      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Active Alerts</p>
                <p className="text-2xl font-bold text-red-600">{activeAlerts.length}</p>
              </div>
              <div className="w-3 h-3 bg-red-500 rounded-full animate-pulse"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Critical Issues</p>
                <p className="text-2xl font-bold text-orange-600">{criticalAlerts.length}</p>
              </div>
              <div className="w-3 h-3 bg-orange-500 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Alerts</p>
                <p className="text-2xl font-bold text-gray-900">{alerts.length}</p>
              </div>
              <div className="w-3 h-3 bg-gray-400 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Alerts List */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle className="flex items-center gap-2">
                Security Alerts
                <div className="w-2 h-2 bg-red-500 rounded-full animate-pulse"></div>
              </CardTitle>
              <CardDescription>
                Real-time security violation monitoring and alerts
              </CardDescription>
            </div>
            <Button variant="outline" size="sm">
              View All
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          {alerts.length === 0 ? (
            <Alert>
              <AlertDescription>
                No security alerts at this time. All systems are operating normally.
              </AlertDescription>
            </Alert>
          ) : (
            <div className="space-y-4">
              {alerts.map((alert) => (
                <div key={alert.id} className="border rounded-lg p-4 space-y-3">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center gap-2 mb-2">
                        {getSeverityBadge(alert.severity)}
                        {getStatusBadge(alert.status)}
                        <span className="text-sm text-gray-500">
                          {new Date(alert.timestamp).toLocaleString()}
                        </span>
                      </div>
                      <h4 className="font-medium text-gray-900 mb-1">
                        {getAlertTypeLabel(alert.type)}
                      </h4>
                      <p className="text-sm text-gray-600 mb-2">
                        {alert.description}
                      </p>
                      <div className="flex items-center gap-4 text-sm text-gray-500">
                        <span>Employee: {alert.employeeName} ({alert.employeeId})</span>
                        <span>Device: {alert.deviceInfo}</span>
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <Button variant="outline" size="sm">
                        Investigate
                      </Button>
                      <Button variant="outline" size="sm">
                        Resolve
                      </Button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
