'use client'

import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { cn } from '@/lib/utils'

interface DashboardSidebarProps {
  activeTab: string
  onTabChange: (tab: string) => void
}

export function DashboardSidebar({ activeTab, onTabChange }: DashboardSidebarProps) {
  const menuItems = [
    {
      id: 'overview',
      label: 'Overview',
      description: 'Dashboard summary'
    },
    {
      id: 'attendance',
      label: 'Attendance',
      description: 'View records'
    },
    {
      id: 'security',
      label: 'Security Alerts',
      description: 'Monitor violations',
      badge: 'Live'
    },
    {
      id: 'devices',
      label: 'Device Management',
      description: 'Manage devices'
    },
    {
      id: 'locations',
      label: 'Location Tracking',
      description: 'GPS monitoring'
    }
  ]

  return (
    <aside className="w-64 bg-white border-r border-gray-200 min-h-screen">
      <div className="p-6">
        <h2 className="text-lg font-semibold text-gray-900 mb-6">Navigation</h2>
        <nav className="space-y-2">
          {menuItems.map((item) => (
            <Button
              key={item.id}
              variant={activeTab === item.id ? "default" : "ghost"}
              className={cn(
                "w-full justify-start h-auto p-3 text-left",
                activeTab === item.id 
                  ? "bg-blue-600 text-white hover:bg-blue-700" 
                  : "text-gray-700 hover:bg-gray-100"
              )}
              onClick={() => onTabChange(item.id)}
            >
              <div className="flex items-center justify-between w-full">
                <div>
                  <div className="font-medium">{item.label}</div>
                  <div className={cn(
                    "text-xs mt-1",
                    activeTab === item.id ? "text-blue-100" : "text-gray-500"
                  )}>
                    {item.description}
                  </div>
                </div>
                {item.badge && (
                  <Badge 
                    variant={activeTab === item.id ? "secondary" : "outline"}
                    className="ml-2 text-xs"
                  >
                    {item.badge}
                  </Badge>
                )}
              </div>
            </Button>
          ))}
        </nav>
      </div>
      
      <div className="absolute bottom-0 left-0 right-0 p-6 border-t border-gray-200">
        <div className="bg-gray-50 rounded-lg p-4">
          <h3 className="font-medium text-gray-900 mb-2">System Status</h3>
          <div className="space-y-2 text-sm">
            <div className="flex justify-between">
              <span className="text-gray-600">Security Level</span>
              <Badge variant="outline" className="bg-green-50 text-green-700 border-green-200">
                High
              </Badge>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">Monitoring</span>
              <span className="text-green-600 font-medium">Active</span>
            </div>
          </div>
        </div>
      </div>
    </aside>
  )
}
