'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'

interface AttendanceRecord {
  id: string
  employeeId: string
  employeeName: string
  qrData: string
  timestamp: string
  location: {
    lat: number
    lng: number
    address: string
  }
  status: 'valid' | 'suspicious' | 'blocked'
  securityScore: number
  deviceInfo: string
}

export function AttendanceTable() {
  const [records, setRecords] = useState<AttendanceRecord[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Simulate API call
    const fetchRecords = async () => {
      setLoading(true)
      try {
        // Simulate API delay
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // Mock data with security information
        const mockRecords: AttendanceRecord[] = [
          {
            id: '1',
            employeeId: 'EMP001',
            employeeName: 'John Smith',
            qrData: 'QR-2024-001-SECURE',
            timestamp: new Date().toISOString(),
            location: {
              lat: 40.7128,
              lng: -74.0060,
              address: 'Office Building A, Floor 3'
            },
            status: 'valid',
            securityScore: 95,
            deviceInfo: 'Samsung Galaxy S23'
          },
          {
            id: '2',
            employeeId: 'EMP002',
            employeeName: 'Sarah Johnson',
            qrData: 'QR-2024-002-SECURE',
            timestamp: new Date(Date.now() - 300000).toISOString(),
            location: {
              lat: 40.7130,
              lng: -74.0058,
              address: 'Office Building A, Floor 2'
            },
            status: 'suspicious',
            securityScore: 65,
            deviceInfo: 'iPhone 14 Pro'
          },
          {
            id: '3',
            employeeId: 'EMP003',
            employeeName: 'Mike Wilson',
            qrData: 'QR-2024-003-SECURE',
            timestamp: new Date(Date.now() - 600000).toISOString(),
            location: {
              lat: 40.7125,
              lng: -74.0062,
              address: 'Office Building A, Floor 1'
            },
            status: 'blocked',
            securityScore: 25,
            deviceInfo: 'OnePlus 11'
          }
        ]
        
        setRecords(mockRecords)
      } catch (error) {
        console.error('Failed to fetch attendance records:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchRecords()
  }, [])

  const getStatusBadge = (status: string, score: number) => {
    switch (status) {
      case 'valid':
        return <Badge className="bg-green-100 text-green-800 border-green-200">Valid</Badge>
      case 'suspicious':
        return <Badge className="bg-yellow-100 text-yellow-800 border-yellow-200">Suspicious</Badge>
      case 'blocked':
        return <Badge className="bg-red-100 text-red-800 border-red-200">Blocked</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  const getSecurityScoreColor = (score: number) => {
    if (score >= 80) return 'text-green-600'
    if (score >= 60) return 'text-yellow-600'
    return 'text-red-600'
  }

  if (loading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Attendance Records</CardTitle>
          <CardDescription>Loading attendance data...</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center h-64">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center justify-between">
          <div>
            <CardTitle>Attendance Records</CardTitle>
            <CardDescription>
              Real-time attendance tracking with security validation
            </CardDescription>
          </div>
          <Button variant="outline" size="sm">
            Export Data
          </Button>
        </div>
      </CardHeader>
      <CardContent>
        {records.length === 0 ? (
          <div className="text-center py-8">
            <p className="text-gray-500">No attendance records found</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Employee</TableHead>
                  <TableHead>QR Code</TableHead>
                  <TableHead>Timestamp</TableHead>
                  <TableHead>Location</TableHead>
                  <TableHead>Security Score</TableHead>
                  <TableHead>Device</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {records.map((record) => (
                  <TableRow key={record.id}>
                    <TableCell>
                      <div>
                        <div className="font-medium">{record.employeeName}</div>
                        <div className="text-sm text-gray-500">{record.employeeId}</div>
                      </div>
                    </TableCell>
                    <TableCell>
                      <code className="text-xs bg-gray-100 px-2 py-1 rounded">
                        {record.qrData}
                      </code>
                    </TableCell>
                    <TableCell>
                      <div className="text-sm">
                        {new Date(record.timestamp).toLocaleDateString()}
                      </div>
                      <div className="text-xs text-gray-500">
                        {new Date(record.timestamp).toLocaleTimeString()}
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className="text-sm">{record.location.address}</div>
                      <div className="text-xs text-gray-500">
                        {record.location.lat.toFixed(4)}, {record.location.lng.toFixed(4)}
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className={`font-medium ${getSecurityScoreColor(record.securityScore)}`}>
                        {record.securityScore}%
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className="text-sm">{record.deviceInfo}</div>
                    </TableCell>
                    <TableCell>
                      {getStatusBadge(record.status, record.securityScore)}
                    </TableCell>
                    <TableCell>
                      <Button variant="outline" size="sm">
                        Details
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
