'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface Device {
  id: string
  employeeId: string
  employeeName: string
  deviceModel: string
  osVersion: string
  appVersion: string
  lastSeen: string
  status: 'online' | 'offline' | 'blocked' | 'suspicious'
  securityScore: number
  location: {
    lat: number
    lng: number
    address: string
  }
  securityFlags: {
    isRooted: boolean
    mockLocationDetected: boolean
    vpnActive: boolean
    debuggingEnabled: boolean
    unknownSources: boolean
  }
  trustLevel: 'high' | 'medium' | 'low' | 'blocked'
}

export function DeviceManagement() {
  const [devices, setDevices] = useState<Device[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedDevice, setSelectedDevice] = useState<Device | null>(null)

  useEffect(() => {
    const fetchDevices = async () => {
      setLoading(true)
      try {
        // Simulate API delay
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // Mock device data
        const mockDevices: Device[] = [
          {
            id: '1',
            employeeId: 'EMP001',
            employeeName: 'John Smith',
            deviceModel: 'Samsung Galaxy S23',
            osVersion: 'Android 14',
            appVersion: '1.2.3',
            lastSeen: new Date().toISOString(),
            status: 'online',
            securityScore: 95,
            location: {
              lat: 40.7128,
              lng: -74.0060,
              address: 'Office Building A, Floor 3'
            },
            securityFlags: {
              isRooted: false,
              mockLocationDetected: false,
              vpnActive: false,
              debuggingEnabled: false,
              unknownSources: false
            },
            trustLevel: 'high'
          },
          {
            id: '2',
            employeeId: 'EMP002',
            employeeName: 'Sarah Johnson',
            deviceModel: 'iPhone 14 Pro',
            osVersion: 'iOS 17.2',
            appVersion: '1.2.3',
            lastSeen: new Date(Date.now() - 300000).toISOString(),
            status: 'suspicious',
            securityScore: 65,
            location: {
              lat: 40.7130,
              lng: -74.0058,
              address: 'Office Building A, Floor 2'
            },
            securityFlags: {
              isRooted: false,
              mockLocationDetected: false,
              vpnActive: true,
              debuggingEnabled: false,
              unknownSources: false
            },
            trustLevel: 'medium'
          },
          {
            id: '3',
            employeeId: 'EMP003',
            employeeName: 'Mike Wilson',
            deviceModel: 'OnePlus 11',
            osVersion: 'Android 13',
            appVersion: '1.2.2',
            lastSeen: new Date(Date.now() - 600000).toISOString(),
            status: 'blocked',
            securityScore: 25,
            location: {
              lat: 40.7125,
              lng: -74.0062,
              address: 'Suspicious Location'
            },
            securityFlags: {
              isRooted: true,
              mockLocationDetected: true,
              vpnActive: false,
              debuggingEnabled: true,
              unknownSources: true
            },
            trustLevel: 'blocked'
          },
          {
            id: '4',
            employeeId: 'EMP004',
            employeeName: 'David Brown',
            deviceModel: 'Samsung Galaxy A54',
            osVersion: 'Android 13',
            appVersion: '1.2.3',
            lastSeen: new Date(Date.now() - 900000).toISOString(),
            status: 'offline',
            securityScore: 80,
            location: {
              lat: 40.7120,
              lng: -74.0055,
              address: 'Office Building A, Floor 1'
            },
            securityFlags: {
              isRooted: false,
              mockLocationDetected: false,
              vpnActive: false,
              debuggingEnabled: false,
              unknownSources: false
            },
            trustLevel: 'high'
          }
        ]
        
        setDevices(mockDevices)
      } catch (error) {
        console.error('Failed to fetch device data:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchDevices()
    
    // Simulate real-time device status updates
    const interval = setInterval(() => {
      setDevices(prev => prev.map(device => ({
        ...device,
        lastSeen: Math.random() > 0.7 ? new Date().toISOString() : device.lastSeen,
        status: Math.random() > 0.9 ? 
          (device.status === 'online' ? 'offline' : 'online') : 
          device.status
      })))
    }, 10000)

    return () => clearInterval(interval)
  }, [])

  const getStatusBadge = (status: string) => {
    switch (status) {
      case 'online':
        return <Badge className="bg-green-100 text-green-800 border-green-200">
          <div className="w-2 h-2 bg-green-500 rounded-full mr-2 animate-pulse"></div>
          Online
        </Badge>
      case 'offline':
        return <Badge className="bg-gray-100 text-gray-800 border-gray-200">Offline</Badge>
      case 'suspicious':
        return <Badge className="bg-yellow-100 text-yellow-800 border-yellow-200">Suspicious</Badge>
      case 'blocked':
        return <Badge className="bg-red-100 text-red-800 border-red-200">Blocked</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  const getTrustLevelBadge = (level: string) => {
    switch (level) {
      case 'high':
        return <Badge className="bg-green-600 text-white">High Trust</Badge>
      case 'medium':
        return <Badge className="bg-yellow-600 text-white">Medium Trust</Badge>
      case 'low':
        return <Badge className="bg-orange-600 text-white">Low Trust</Badge>
      case 'blocked':
        return <Badge className="bg-red-600 text-white">Blocked</Badge>
      default:
        return <Badge variant="outline">Unknown</Badge>
    }
  }

  const getSecurityScoreColor = (score: number) => {
    if (score >= 80) return 'text-green-600'
    if (score >= 60) return 'text-yellow-600'
    return 'text-red-600'
  }

  const handleDeviceAction = (deviceId: string, action: 'block' | 'unblock' | 'reset') => {
    setDevices(prev => prev.map(device => 
      device.id === deviceId 
        ? { 
            ...device, 
            status: action === 'block' ? 'blocked' : action === 'unblock' ? 'online' : device.status,
            trustLevel: action === 'block' ? 'blocked' : action === 'unblock' ? 'medium' : device.trustLevel
          }
        : device
    ))
  }

  if (loading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Device Management</CardTitle>
          <CardDescription>Loading device information...</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center h-64">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
          </div>
        </CardContent>
      </Card>
    )
  }

  const onlineDevices = devices.filter(d => d.status === 'online').length
  const blockedDevices = devices.filter(d => d.status === 'blocked').length
  const suspiciousDevices = devices.filter(d => d.status === 'suspicious').length

  return (
    <div className="space-y-6">
      {/* Device Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Devices</p>
                <p className="text-2xl font-bold text-gray-900">{devices.length}</p>
              </div>
              <div className="w-3 h-3 bg-gray-400 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Online</p>
                <p className="text-2xl font-bold text-green-600">{onlineDevices}</p>
              </div>
              <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Suspicious</p>
                <p className="text-2xl font-bold text-yellow-600">{suspiciousDevices}</p>
              </div>
              <div className="w-3 h-3 bg-yellow-500 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Blocked</p>
                <p className="text-2xl font-bold text-red-600">{blockedDevices}</p>
              </div>
              <div className="w-3 h-3 bg-red-500 rounded-full"></div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Security Alerts */}
      {blockedDevices > 0 && (
        <Alert>
          <AlertDescription>
            {blockedDevices} device(s) are currently blocked due to security violations. 
            Review and take appropriate action.
          </AlertDescription>
        </Alert>
      )}

      {/* Device Table */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle>Registered Devices</CardTitle>
              <CardDescription>
                Monitor and manage all registered devices with security status
              </CardDescription>
            </div>
            <Button variant="outline" size="sm">
              Add Device
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Employee</TableHead>
                  <TableHead>Device</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Security Score</TableHead>
                  <TableHead>Trust Level</TableHead>
                  <TableHead>Last Seen</TableHead>
                  <TableHead>Security Flags</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {devices.map((device) => (
                  <TableRow key={device.id}>
                    <TableCell>
                      <div>
                        <div className="font-medium">{device.employeeName}</div>
                        <div className="text-sm text-gray-500">{device.employeeId}</div>
                      </div>
                    </TableCell>
                    <TableCell>
                      <div>
                        <div className="font-medium">{device.deviceModel}</div>
                        <div className="text-sm text-gray-500">
                          {device.osVersion}  App v{device.appVersion}
                        </div>
                      </div>
                    </TableCell>
                    <TableCell>
                      {getStatusBadge(device.status)}
                    </TableCell>
                    <TableCell>
                      <div className={`font-medium ${getSecurityScoreColor(device.securityScore)}`}>
                        {device.securityScore}%
                      </div>
                    </TableCell>
                    <TableCell>
                      {getTrustLevelBadge(device.trustLevel)}
                    </TableCell>
                    <TableCell>
                      <div className="text-sm">
                        {new Date(device.lastSeen).toLocaleDateString()}
                      </div>
                      <div className="text-xs text-gray-500">
                        {new Date(device.lastSeen).toLocaleTimeString()}
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className="space-y-1">
                        {device.securityFlags.isRooted && (
                          <Badge variant="destructive" className="text-xs">Root</Badge>
                        )}
                        {device.securityFlags.mockLocationDetected && (
                          <Badge variant="destructive" className="text-xs">Mock GPS</Badge>
                        )}
                        {device.securityFlags.vpnActive && (
                          <Badge variant="secondary" className="text-xs">VPN</Badge>
                        )}
                        {device.securityFlags.debuggingEnabled && (
                          <Badge variant="secondary" className="text-xs">Debug</Badge>
                        )}
                        {!Object.values(device.securityFlags).some(flag => flag) && (
                          <Badge variant="outline" className="text-xs">Clean</Badge>
                        )}
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className="flex gap-1">
                        {device.status !== 'blocked' ? (
                          <Button 
                            variant="outline" 
                            size="sm"
                            onClick={() => handleDeviceAction(device.id, 'block')}
                          >
                            Block
                          </Button>
                        ) : (
                          <Button 
                            variant="outline" 
                            size="sm"
                            onClick={() => handleDeviceAction(device.id, 'unblock')}
                          >
                            Unblock
                          </Button>
                        )}
                        <Button 
                          variant="outline" 
                          size="sm"
                          onClick={() => setSelectedDevice(device)}
                        >
                          Details
                        </Button>
                      </div>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>

      {/* Device Details Modal */}
      {selectedDevice && (
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle>Device Details: {selectedDevice.deviceModel}</CardTitle>
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setSelectedDevice(null)}
              >
                Close
              </Button>
            </div>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Device Information</h4>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-gray-600">Model:</span>
                      <span>{selectedDevice.deviceModel}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">OS Version:</span>
                      <span>{selectedDevice.osVersion}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">App Version:</span>
                      <span>{selectedDevice.appVersion}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">Security Score:</span>
                      <span className={getSecurityScoreColor(selectedDevice.securityScore)}>
                        {selectedDevice.securityScore}%
                      </span>
                    </div>
                  </div>
                </div>
                
                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Security Flags</h4>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-gray-600">Rooted Device:</span>
                      <Badge variant={selectedDevice.securityFlags.isRooted ? "destructive" : "outline"}>
                        {selectedDevice.securityFlags.isRooted ? "Yes" : "No"}
                      </Badge>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">Mock Location:</span>
                      <Badge variant={selectedDevice.securityFlags.mockLocationDetected ? "destructive" : "outline"}>
                        {selectedDevice.securityFlags.mockLocationDetected ? "Detected" : "Clean"}
                      </Badge>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">VPN Active:</span>
                      <Badge variant={selectedDevice.securityFlags.vpnActive ? "secondary" : "outline"}>
                        {selectedDevice.securityFlags.vpnActive ? "Yes" : "No"}
                      </Badge>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">Debugging:</span>
                      <Badge variant={selectedDevice.securityFlags.debuggingEnabled ? "secondary" : "outline"}>
                        {selectedDevice.securityFlags.debuggingEnabled ? "Enabled" : "Disabled"}
                      </Badge>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="space-y-4">
                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Location Information</h4>
                  <div className="space-y-2 text-sm">
                    <div>
                      <span className="text-gray-600">Address:</span>
                      <p>{selectedDevice.location.address}</p>
                    </div>
                    <div>
                      <span className="text-gray-600">Coordinates:</span>
                      <p>{selectedDevice.location.lat.toFixed(6)}, {selectedDevice.location.lng.toFixed(6)}</p>
                    </div>
                    <div>
                      <span className="text-gray-600">Last Update:</span>
                      <p>{new Date(selectedDevice.lastSeen).toLocaleString()}</p>
                    </div>
                  </div>
                </div>
                
                <div>
                  <h4 className="font-medium text-gray-900 mb-2">Actions</h4>
                  <div className="space-y-2">
                    <Button 
                      variant="outline" 
                      className="w-full"
                      onClick={() => handleDeviceAction(selectedDevice.id, 'reset')}
                    >
                      Reset Security Score
                    </Button>
                    <Button 
                      variant={selectedDevice.status === 'blocked' ? "default" : "destructive"}
                      className="w-full"
                      onClick={() => handleDeviceAction(
                        selectedDevice.id, 
                        selectedDevice.status === 'blocked' ? 'unblock' : 'block'
                      )}
                    >
                      {selectedDevice.status === 'blocked' ? 'Unblock Device' : 'Block Device'}
                    </Button>
                  </div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
