'use client'

import { useState, useEffect } from 'react'
import { DashboardHeader } from '@/components/DashboardHeader'
import { DashboardSidebar } from '@/components/DashboardSidebar'
import { AttendanceTable } from '@/components/AttendanceTable'
import { SecurityAlertsPanel } from '@/components/SecurityAlertsPanel'
import { LocationTrackingMap } from '@/components/LocationTrackingMap'
import { DeviceManagement } from '@/components/DeviceManagement'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

export default function DashboardPage() {
  const [activeTab, setActiveTab] = useState('overview')
  const [securityAlerts, setSecurityAlerts] = useState(0)
  const [onlineDevices, setOnlineDevices] = useState(0)
  const [todayAttendance, setTodayAttendance] = useState(0)

  useEffect(() => {
    // Simulate real-time data updates
    const interval = setInterval(() => {
      setSecurityAlerts(Math.floor(Math.random() * 5))
      setOnlineDevices(Math.floor(Math.random() * 20) + 10)
      setTodayAttendance(Math.floor(Math.random() * 100) + 50)
    }, 5000)

    return () => clearInterval(interval)
  }, [])

  const renderContent = () => {
    switch (activeTab) {
      case 'overview':
        return (
          <div className="space-y-6">
            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Security Alerts</CardTitle>
                  <Badge variant={securityAlerts > 2 ? "destructive" : "secondary"}>
                    {securityAlerts > 2 ? "High" : "Normal"}
                  </Badge>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{securityAlerts}</div>
                  <p className="text-xs text-muted-foreground">Active violations</p>
                </CardContent>
              </Card>
              
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Online Devices</CardTitle>
                  <Badge variant="outline">{onlineDevices}</Badge>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{onlineDevices}</div>
                  <p className="text-xs text-muted-foreground">Currently active</p>
                </CardContent>
              </Card>
              
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Today's Attendance</CardTitle>
                  <Badge variant="outline">Live</Badge>
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{todayAttendance}</div>
                  <p className="text-xs text-muted-foreground">Verified check-ins</p>
                </CardContent>
              </Card>
            </div>

            {/* Security Alerts Panel */}
            <SecurityAlertsPanel />
            
            {/* Location Tracking */}
            <LocationTrackingMap />
          </div>
        )
      case 'attendance':
        return <AttendanceTable />
      case 'security':
        return <SecurityAlertsPanel />
      case 'devices':
        return <DeviceManagement />
      case 'locations':
        return <LocationTrackingMap />
      default:
        return <AttendanceTable />
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <DashboardHeader />
      <div className="flex">
        <DashboardSidebar activeTab={activeTab} onTabChange={setActiveTab} />
        <main className="flex-1 p-6">
          <div className="max-w-7xl mx-auto">
            {renderContent()}
          </div>
        </main>
      </div>
    </div>
  )
}
